import React, { Component } from 'react'
import { Step, Icon } from 'semantic-ui-react'

export default class Stepper extends Component {

  handleActive = (step) => {
    switch (this.props.step) {
      case 1:
        switch (step) {
          case 1:
            return 'active'
          case 2:
            return 'disabled'
          case 3:
            return 'disabled'
        }
        break;
      case 2:
        switch (step) {
          case 1:
            return 'completed'
          case 2:
            return 'active'
          case 3:
            return 'disabled'
        }
        break;
      case 3:
        switch (step) {
          case 1:
            return 'completed'
          case 2:
            return 'completed'
          case 3:
            return 'active'
        }

    }
  }
  render() {
    return (
      <Step.Group fluid>
        <Step className={this.handleActive(1)}>
          <Icon name='user' />
          <Step.Content>
            <Step.Title>Personal Information</Step.Title>
            <Step.Description>Enter details</Step.Description>
          </Step.Content>
        </Step>

        <Step className={this.handleActive(2)}>
          <Icon name='payment' />
          <Step.Content>
            <Step.Title>Billing</Step.Title>
            <Step.Description>Enter billing information</Step.Description>
          </Step.Content>
        </Step>

        <Step className={this.handleActive(3)}>
          <Icon name='info' />
          <Step.Content>
            <Step.Title>Confirm Subscription</Step.Title>
          </Step.Content>
        </Step>
      </Step.Group>
    )
  }
}