import React, { Component } from 'react'
import Step, { Grid, Sidebar, Segment, Icon, Menu, Header, Image, Card, Button, Modal } from 'semantic-ui-react'
import Stepper from './Stepper'
import Details from './Details'

export default class Feed extends Component {
    state = {
        animation: 'push',
        direction: 'left',
        dimmed: false,
        visible: true,
        open: false,
        step: 1,
        label: 'Next',
        icon: 'arrow circle right'
    }

    data = [
        {
            name: 'Event 1',
            description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis id gravida nunc, a ornare tellus. Donec vehicula erat in mi molestie, eu faucibus turpis porttitor. Vestibulum tincidunt a mi id porttitor.',
            date_added: '11/11/19',
            subscribers: '2'
        },
        {
            name: 'Event 2',
            description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis id gravida nunc, a ornare tellus. Donec vehicula erat in mi molestie, eu faucibus turpis porttitor. Vestibulum tincidunt a mi id porttitor.',
            date_added: '11/11/19',
            subscribers: '2'
        },
        {
            name: 'Event 3',
            description: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit. Duis id gravida nunc, a ornare tellus. Donec vehicula erat in mi molestie, eu faucibus turpis porttitor. Vestibulum tincidunt a mi id porttitor.',
            date_added: '11/11/19',
            subscribers: '2'
        },


    ]

    renderCards = () => {
        var arr = []

        for (let value of this.data) {
            arr.push(
                <Grid.Row>
                    <Card style={{ width: '50%', margin: '0 auto', border: 'none', marginTop: 20 }}>
                        <Image src='images/image.png' wrapped ui={false} />
                        <Card.Content>
                            <Card.Header>{value.name}</Card.Header>
                            <Card.Meta>
                                <span className='date'>{value.date_added}</span>
                            </Card.Meta>
                            <Card.Description>
                                {value.description}
                            </Card.Description>
                        </Card.Content>
                        <Card.Content extra>
                            <a onClick={() => this.setState({ open: true })}>
                                <Icon name='share' />
                                Subscribe
                        </a>
                        </Card.Content>
                    </Card>
                </Grid.Row>
            )
        }

        return arr;

    }

    nextStep = (step) => {
        switch (step) {
            case 1:
                this.setState({ step: 2 })
                break;
            case 2:
                this.setState({ step: 3, label: 'Download App', icon: 'download' })
                break;
            default:
                break;
        }
    }

    render() {
        const { animation, direction, visible, open, step, label, icon } = this.state
        return (
            <Sidebar.Pushable as={Segment} style={{ marginTop: 30, marginBottom: 30, 'overflow-y': 'auto' }}>
                <Sidebar
                    as={Menu}
                    animation='push'
                    icon='labeled'
                    vertical
                    visible={this.state.visible}
                    width='thin'
                >
                    <Menu.Item as='a'>
                        Birthday
              </Menu.Item>
                    <Menu.Item as='a'>

                        Wedding
              </Menu.Item>
                    <Menu.Item as='a'>

                        Socials
              </Menu.Item>
                    <Menu.Item as='a'>

                        Anniversary
              </Menu.Item>
                    <Menu.Item as='a'>

                        Others
              </Menu.Item>

                </Sidebar>
                <Sidebar.Pusher>
                    <Grid columns="equal">
                        <Grid.Row className="row-wrapper" >
                            <Grid.Column width={1}>
                                <Icon style={{ paddingLeft: 15, paddingTop: 8, cursor: 'pointer' }} name="bars" onClick={() => this.setState({ visible: !visible })} />
                            </Grid.Column>
                            <Grid.Column width={15} style={{ 'text-align': 'center', 'overflow': 'auto' }}>
                                <Card className="pusher-card">
                                    {this.renderCards()}
                                </Card>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                </Sidebar.Pusher>
                <Modal closeOnEscape={true} dimmer="blurring" open={open} centered={true} style={{ width: '80%' }}>
                    <Modal.Header style={{ 'text-align': 'right' }}>
                        <Grid>
                            <Grid.Column>
                                <Grid.Row style={{ 'text-align': 'right' }}>
                                    <Icon name="times" onClick={() => this.setState({ open: false, step: 1, icon: 'arrow circle right', label: 'Next' })} />
                                </Grid.Row>
                                <Grid.Row style={{ 'text-align': 'center' }}>
                                    <Stepper step={step} />
                                </Grid.Row>
                            </Grid.Column>
                        </Grid>
                    </Modal.Header>
                    <Modal.Content style={{ 'text-align': 'center' }}>
                        <Modal.Description>
                            <Details />
                            <Button icon="arrow circle right" content="Subscribe" onClick={() => this.nextStep(step)} size="large" color="linkedin" style={{ 'border-radius': '25px', color: "white", marginTop: 70, marginBottom: 20 }}><p style={{ display: 'inline', paddingRight: 5 }}>{label}</p><Icon name={icon} /></Button>
                        </Modal.Description>
                    </Modal.Content>
                </Modal>
            </Sidebar.Pushable>
        )
    }
}